  background-color: #078fd1;
  /* NEED TO REMOVE TWINK */
  background-image: linear-gradient(to bottom, #02a9d5, #078fd1); }

.btn-primary:hover {
  background-color: #0790d1; }

.btn-primary:not(:disabled):not(.disabled).active,
