/**

  @1 - Desktop Styles
  @2 - Mobile Styles
  @3 - Main Styles
  @4 - Steps Styles

  Styles depends on global settings for the material-ui and theme styles material-theme.scss

 */


$colorPrimary: #00C6A2;
$colorPrimaryLight: #23EEC9;
$colorPrimarySecond: #2B6B6A;
$colorErrorPrimary: #FF522A;
$colorWhite: #FFFFFF;
$colorGrayLight: #EDEDED;
$colorGrayLightSecond: #E9E9E9;
$colorGray: #9E9E9E;
$colorGraySecond: #545454;
$colorShadow: rgba(0, 0, 0, 0.13);

$calculatorMinHeightDesktop: 665px;
$calculatorMinHeightMobile: 812px;


//@function calculateRem($size) {
//  $remSize: $size / 16px;
//  @return $remSize * 1rem;
//}

.CO2Calculator {
  width: 100%;
  height: 100%;

  /**
    @1 - Styles for the Desktop Version
   */

  &.Desktop {
    padding: 40px 20px 40px 80px;
    min-height: $calculatorMinHeightDesktop;

    background: white;

    /**
      @4 - Styles for Special Steps
   */

    &.step-0 {
      background: url("./img/MaskGroup(1).png") bottom center/contain no-repeat white;
    }

    &.step-1 {
      background: url("./img/MaskGroup(2).png") bottom center/contain no-repeat white;
    }

    &.step-2 {
      background: url("./img/MaskGroup(3).png") bottom center/contain no-repeat white;
    }

    &.step-3 {
      background: url("./img/MaskGroup(4).png") bottom center/contain no-repeat white;
    }

    &.step-4 {
      background: url("./img/MaskGroup(5).png") bottom center/contain no-repeat white;
    }

    &.step-5 {
      background: url("./img/MaskGroup(6).png") bottom center/contain no-repeat white;
    }

    &.step-0 {
      .CO2Calculator {
        &__Title {
          &__Value {
            font-size: 30px;
          }
        }
      }
    }

    &.step-2 {
      .CO2Calculator__Content {
        padding: 30px 30px 30px 45px;
      }

      .CO2Calculator__Title {
        h2 {
          margin-bottom: 25px;
        }
      }

      .form-col-label {
        flex: 1 0 125px;
      }
    }

    &.step-3 {
      .form-col-label {
        flex: 1 0 30%;
      }
    }

    &.step-4 {
      .CO2Calculator__Content {
        padding: 0;
        border-color: transparent;
        background-color: transparent;

        .result-lead {
          padding: 0;
          margin: 0;
          font-size: 20px;
          font-weight: 500;
          color: $colorGraySecond;
        }

        .result-lead-small {
          max-width: 50%;
          margin: 40px 0;
          font-size: 14px;
          color: $colorGraySecond;
        }

        .result-form-part {
          max-width: 50%;
        }

      }

      .content-nav {
        justify-content: flex-start;
      }

      .content-col.col8 {
        justify-content: flex-start;
      }
    }

    &.step-5 {

      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      .content-row {
        width: 70%;
      }

      .col-first {
        display: none;
      }

      .col-second {
        flex: 0 0 100%;
        max-width: 100%
      }

      .content-nav {
        .content-col.col4 {
          display: none;
        }

        .content-col.col8 {
          flex: 0 0 100%;
          justify-content: flex-start;
        }
      }

      .content-nav {
        padding: 0;
      }

      .CO2Calculator__Content {
        padding: 0;
        border-color: transparent;
        background-color: transparent;

        .done {
          margin-bottom: 40px;
        }

        .done-lead {
          margin-bottom: 40px;
          font-size: 30px;
          font-weight: 500;
          line-height: 40px;
          color: $colorPrimarySecond;
        }

        .done-lead-small {
          font-size: 20px;
          color: $colorGraySecond;
        }
      }
    }

    .Stepper {
      .MuiStepIcon-text {
        display: none;
      }

      .MuiSvgIcon-root {
        width: 7px;
        height: 7px;
        transform: translate3d(9px, 0, 0);
      }

      .MuiStepConnector-root {
        padding: 0;
        min-height: 75px;

        .MuiStepConnector-lineVertical {
          min-height: 75px;
        }
      }

      .MuiStepLabel-root {
        position: relative;
      }

      .MuiStepLabel-label {
        position: absolute;
        top: 100%;
        left: 0;
        transform: translate3d(35px, 25px, 0);
        font-size: 20px;
      }

      .MuiStep-vertical {
        position: relative;

        &:last-of-type {
          .MuiStepLabel-labelContainer {
            display: none;
          }
        }
      }

      .MuiStepLabel-vertical {
        &.Index {

          .MuiSvgIcon-root {
            display: none;
          }

          .MuiStepLabel-label {
            transform: translate3d(-25px, 25px, 0);
          }
        }
      }

      .MuiStepConnector-active .MuiStepConnector-line:before {
        background: transparent !important;
      }

      .MuiStepConnector-active .MuiStepConnector-line {
        border-color: $colorPrimary;
      }

      .MuiTypography-root.MuiStepLabel-label.MuiStepLabel-completed {
        color: $colorPrimary;
      }

      .MuiStepLabel-iconContainer {
        .MuiSvgIcon-root.MuiStepIcon-root.MuiStepIcon-active,
        .MuiSvgIcon-root.MuiStepIcon-root.MuiStepIcon-completed {
          border-radius: 50%;
          box-shadow: 0 0 10px $colorPrimary, 0 0 6px $colorWhite, inset 0 2px 3px $colorPrimaryLight, inset 0 -2px 2px $colorShadow;
        }
      }

      .MuiStep-completed {
        .Index {
          &:after {
            content: '';
            display: block;
            width: 7px;
            height: 7px;
            background: $colorPrimary;
            border-radius: 50%;
            position: absolute;
            transform: translate3d(9px, -4px, 0);
            z-index: 200;
          }
        }
      }

    }

    .CO2Calculator {
      &__Title {
        margin-bottom: 20px;

        h2 {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          align-items: center;
          font-size: 30px;
          font-weight: 500;
          line-height: 40px;
          color: $colorPrimarySecond !important;
        }

        &__Label {
          display: inline-block;
          padding: 0 4px;
          font-weight: 500;
          line-height: 159.61%;
          color: $colorErrorPrimary;
        }

        &__Value {
          display: flex;
          justify-content: center;
          align-items: center;
          text-align: center;
          margin: 0 10px 0 6px;
          width: 76px;
          height: 76px;
          border-radius: 50%;
          color: white;
          font-size: 19px;
          font-weight: 400;
          background: linear-gradient(47.67deg, $colorErrorPrimary 14.82%, rgba(255, 82, 42, 0) 212.62%, $colorErrorPrimary 212.62%);
        }

        &__Size {
          font-size: 18px;
          line-height: 159.61%;
          color: $colorErrorPrimary;
        }
      }

      &__Content {
        padding: 30px 65px;
        border: 1px solid rgba(84, 84, 84, .2);
        border-radius: 4px;
        background: white;
      }
    }

    .MuiStepConnector-active,
    .MuiStepConnector-completed {
      .MuiStepConnector-line {
        border-color: $colorPrimary !important;
      }
    }

  }

  /**
    @2 Styles for the Mobile Version
   */

  &.Mobile {
    min-height: $calculatorMinHeightMobile;
    padding: 0 15px;

    background: white;

    /**
      @4 - Styles for Special Steps
   */

    &.step-0 {
      background: url("./img/mobile/MaskGroup(1)-mobile.png") bottom center/cover no-repeat white;
    }

    &.step-1 {
      background: url("./img/mobile/MaskGroup(2)-mobile.png") bottom center/cover no-repeat white;
    }

    &.step-2 {
      background: url("./img/mobile/MaskGroup(3)-mobile.png") bottom center/cover no-repeat white;
    }

    &.step-3 {
      background: url("./img/mobile/MaskGroup(4)-mobile.png") bottom center/cover no-repeat white;
    }

    &.step-4 {
      background: url("./img/mobile/MaskGroup(5)-mobile.png") bottom center/cover no-repeat white;
    }

    &.step-5 {
      background: url("./img/mobile/MaskGroup(6)-mobile.png") bottom center/cover no-repeat white;
    }

    &.step-0 {
      .CO2Calculator {
        &__Title {
          &__Value {
            font-size: 30px;
          }
        }
      }
    }

    &.step-1 {
      .Step__Label-Mobile {
        margin-bottom: 80px;
      }
    }

    &.step-2 {

      .form-combine-inputs {
        .form-combine-input {
          width: 48%;
        }
      }

      .Step__Label-Mobile {
        margin-bottom: 20px;
      }
    }

    &.step-3 {
      .Step__Label-Mobile {
        margin-bottom: 30px;
      }
      .btn-navigation.prev {
        .btn-symbol {
          color: $colorPrimary;
        }
      }
    }


    &.step-4 {
      .CO2Calculator__Content {
        padding: 0;
        border-color: transparent;
        background-color: transparent;

        .result-lead {
          padding: 0;
          margin: 0;
          font-size: 16px;
          font-weight: 500;
          color: $colorGraySecond;
        }

        .result-lead-small {
          margin: 20px 0;
          font-size: 14px;
          color: $colorGraySecond;
        }
      }
    }

    &.step-5 {
      .CO2Calculator__Content {
        border-color: transparent;
        background-color: transparent;

        .done {
          padding-top: 200px;
        }

        .done-lead {
          margin-bottom: 30px;
          font-size: 24px;
          font-weight: 500;
          text-align: center;
          line-height: 40px;
          color: $colorPrimarySecond;
        }

        .done-lead-small {
          font-size: 18px;
          text-align: center;
          color: $colorGraySecond;
        }
      }
    }

    .content-row {
      flex-direction: column;
    }

    .content-row.content-nav {
      .content-col {
        justify-content: center;
      }
    }

    .Stepper {

      .MuiStepIcon-root {
        color: $colorGrayLight;
        opacity: 1;
      }

      .MuiStepIcon-root.MuiStepIcon-active,
      .MuiStepIcon-root.MuiStepIcon-completed {
        color: $colorPrimary;
      }

      .MuiStepIcon-text {
        display: none;
      }

      .MuiSvgIcon-root {
        position: absolute;
        width: 7px;
        height: 7px;
        transform: translate3d(0, 9px, 0);
        z-index: 100;
      }

      .MuiStepLabel-label.MuiStepLabel-alternativeLabel {
        font-size: 20px;
        transform: translate3d(calc(50% + 10px), -45px, 0);
      }

      .MuiStep-horizontal {
        position: relative;

        &:last-of-type {
          .MuiStepLabel-labelContainer {
            display: none;
          }
        }
      }

      .MuiStepLabel-iconContainer {
        .MuiSvgIcon-root.MuiStepIcon-root.MuiStepIcon-active,
        .MuiSvgIcon-root.MuiStepIcon-root.MuiStepIcon-completed {
          border-radius: 50%;
          box-shadow: 0 0 10px $colorPrimary, 0 0 6px $colorWhite, inset 0 2px 3px $colorPrimaryLight, inset 0 -2px 2px $colorShadow;
        }
      }

      .MuiStepConnector-active .MuiStepConnector-line:before {
        background: $colorPrimary !important;
      }

      .MuiStep-completed {
        .MuiStepLabel-iconContainer {
          &:after {
            content: '';
            display: block;
            width: 7px;
            height: 7px;
            background: $colorPrimary;
            border-radius: 50%;
            position: absolute;
            transform: translate3d(0, 9px, 0);
            z-index: 200;
          }
        }
      }

    }

    .Step__Label-Mobile {
      margin: 0;
      text-align: center;
      font-size: 20px;
      color: $colorPrimary;
      font-weight: 500;
    }

    .CO2Calculator {
      &__Title {
        h2 {
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          font-size: 24px;
          font-weight: 500;
          line-height: 32px;
          color: $colorPrimarySecond !important;
        }

        &__Label {
          display: inline-block;
          padding: 0 4px;
          font-weight: 500;
          line-height: 159.61%;
          color: $colorErrorPrimary;
        }

        &__Value {
          display: flex;
          justify-content: center;
          align-items: center;
          text-align: center;
          margin: 0 10px 0 6px;
          width: 76px;
          height: 76px;
          border-radius: 50%;
          color: white;
          font-size: 19px;
          font-weight: 400;
          background: linear-gradient(47.67deg, $colorErrorPrimary 14.82%, rgba(255, 82, 42, 0) 212.62%, $colorErrorPrimary 212.62%);
        }

        &__Size {
          font-size: 18px;
          line-height: 159.61%;
          color: $colorErrorPrimary;
        }
      }
    }

    .wrapper-one {
      margin-bottom: 20px;
      width: 100%;
      text-align: center;
      justify-content: center;
    }

    .wrapper-two {
      padding-left: 20px;
      width: 100%;
      text-align: center;
      justify-content: center;
    }

    .form-row {
      margin: 0;
      flex-direction: column;
    }

    .form-col {
      width: 100%;
      min-height: 50px;
      padding: 0 30px;
    }

    .form-label {
      padding: 0;
      min-height: 50px;
    }

    .MuiPaper-root {
      padding: 24px 0 0 0;
    }

    .MuiFormControl-root {
      transform: translate3d(0, -15px, 0);
    }

    .form-label-index {
      transform: translate3d(-35px, -11px, 0);
    }

    .content-nav {
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }

    .btn-navigation.prev {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      min-width: 45px;
      width: 45px;
      height: 45px;
      border-radius: 50%;
      border: 2px solid $colorPrimary;
      background: rgba(255, 255, 255, 0.2);

      &.white {
        color: white;
        border-color: white;
      }

      .btn-text {
        display: none;
      }

      .btn-symbol {
        padding: 0;
        transform: scale(1.4) translate3d(0, 0, 0);
        color: white;
      }
    }

    .MuiStepLabel-completed {
      color: $colorPrimary;
    }

    .MuiStepConnector-active,
    .MuiStepConnector-completed {
      .MuiStepConnector-line {
        border-color: $colorPrimary;
      }
    }
  }

  /**
    @3 - Main Styles
   */

  .CO2Calculator__Content {
    position: relative;
  }

  .MuiStepLabel-label.MuiStepLabel-active.MuiStepLabel-alternativeLabel {
    margin-top: 16px !important;
  }

  .MuiPaper-root {
    background-color: transparent;
  }

  .col-first {
    order: 1;
  }

  .col-second {
    order: 2;
  }

  .col-third {
    order: 3;
  }

  .wrapper-one {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .wrapper-two {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .form-row {
    margin-bottom: 20px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .form-col {
    width: 50%;
    flex-grow: 1;
    display: flex;
    align-items: center;
  }

  .form-label {
    position: relative;
    padding-top: 15px;
    width: 100%;
    min-height: 60px;
    margin: 0;
    display: flex;
    align-items: center;

    &-index {
      position: absolute;
      left: 0;
      top: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 10px;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      border: 1px solid $colorPrimarySecond;
      text-align: center;
      font-size: 12px;
      font-weight: 500;
      color: $colorPrimarySecond;
      transform: translate3d(-35px, -25%, 0);
    }

    &-text {
      font-size: 18px;
      font-weight: 500;
      color: $colorGraySecond;
    }
  }

  .form-combine-inputs {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .MuiFormControl-root {
    position: relative;

    .form-input-unit {
      position: absolute;
      top: 50%;
      right: 0;
      font-size: 16px;
      line-height: 21px;
      color: $colorGraySecond;
      transform: translate3d(0, -50%, 0);
    }
  }

  .p0 {
    padding: 0 !important;
  }

  .mr1 {
    margin-right: 16px;
  }

  .dNone {
    display: none;
  }

  .content-row {
    width: 100%;
    display: flex;
    flex-direction: row;
  }

  .content-col {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .col12 {
    width: 100%;
    flex: 0 0 100%;
  }

  .col8 {
    width: 66.666%;
    flex: 0 0 66.666%;
  }

  .col4 {
    width: 33.333%;
    flex: 0 0 33.333%;
  }

  .content-nav {
    padding: 30px 15px;
    justify-content: flex-end;
  }

  .content-row.content-nav {
    .content-col {
      flex-direction: row;
      justify-content: flex-end;
    }
  }

  .btn-navigation {
    .btn-symbol {
      display: inline-block;
      transform: scale(1.4) translate3d(0, 0, 0);
    }

    &.next {
      .btn-symbol {
        padding-left: 15px;
      }

      &.Mui-disabled {
        color: $colorErrorPrimary !important;
      }
    }

    &.prev {
      .btn-symbol {
        padding-right: 15px;
      }
    }
  }

  .MuiStepLabel-label {
    color: $colorGraySecond;
    opacity: .2;
    font-weight: 500;
  }

  .MuiStepLabel-label.MuiStepLabel-active,
  .MuiStepLabel-label.MuiStepLabel-completed {
    opacity: 1;
  }

  .MuiStepIcon-root {
    border-radius: 50%;
    color: $colorGrayLightSecond;
    box-shadow: 0 0 6px white, inset 0 2px 3px $colorGrayLight, inset 0 -2px 2px rgba(0, 0, 0, 0.13);
    transform: rotate(90deg);
  }

  .MuiStepConnector-line {
    border-color: $colorGrayLightSecond;
  }

  .MuiButton-outlinedPrimary {
    color: $colorPrimary !important;
  }

  .MuiStepConnector-alternativeLabel {
    left: calc(-50%);
    right: calc(50%);
  }

}