/* ___BUTTONS___ */
.btn {
    text-transform: uppercase;
    font-weight: bold;
    min-width: 140px;
}

.btn:hover {
    box-shadow: 0 0 40px -10px rgba(0, 0, 0, .5);
}

.btn:not(:disabled):not(.disabled).active,
.btn:not(:disabled):not(.disabled):active {
    background-image: inherit;
}

/* DANGER BUTTON */
.btn-danger {
    background-color: #fd5833; /* NEED TO REMOVE TWINK */
    background-image: linear-gradient(to bottom, #fd7a5c, #fd5832);
}

.btn-danger:hover {
    background-color: #fd6744;
}

.btn-danger:not(:disabled):not(.disabled).active,
.btn-danger:not(:disabled):not(.disabled):active,
.show > .btn-danger.dropdown-toggle {
    background-color: #fd5832;
}

.btn-danger.focus,
.btn-danger:focus,
.btn-danger:not(:disabled):not(.disabled).active:focus,
.btn-danger:not(:disabled):not(.disabled):active:focus,
.show > .btn-danger.dropdown-toggle:focus {
    box-shadow: 0 0 0 3px rgba(243, 103, 68, .25);
}

/* PRIMARY BUTTON */
.btn-primary {
    background-color: #078fd1; /* NEED TO REMOVE TWINK */
    background-image: linear-gradient(to bottom, #02a9d5, #078fd1);
}

.btn-primary:hover {
    background-color: #0790d1;
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show > .btn-primary.dropdown-toggle {
    background-color: #078fd1;
}

.btn-primary:not(:disabled):not(.disabled).active:focus,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show>.btn-primary.dropdown-toggle:focus {
    box-shadow: 0 0 0 3px rgba(7, 134, 199, .25);
}

/* WARNING BUTTON */
.btn-warning {
    background-color: #f99834; /* NEED TO REMOVE TWINK */
    background-image: linear-gradient(to bottom, #ffc831, #f99834);
}

.btn-warning,
.btn-warning:hover,
.btn:not(:disabled):not(.disabled).active,
.btn:not(:disabled):not(.disabled):active,
.btn-warning:not(:disabled):not(.disabled).active,
.btn-warning:not(:disabled):not(.disabled):active,
.show>.btn-warning.dropdown-toggle
{
    color: #fff;
}

.btn-warning:hover {
     background-color: #fcaf33;
}

.btn-warning:not(:disabled):not(.disabled).active,
.btn-warning:not(:disabled):not(.disabled):active,
.show>.btn-warning.dropdown-toggle {
    background-color: #f99834;
}

.btn-warning:not(:disabled):not(.disabled).active:focus,
.btn-warning:not(:disabled):not(.disabled):active:focus,
.show>.btn-warning.dropdown-toggle:focus {
    box-shadow: 0 0 0 3px rgba(239, 141, 53, .25);
}

/* ___BUTTONS___ */
.navbar-light .navbar-toggler {
    border-color: rgba(255, 255, 255, 1);
}

.navbar-light .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255, 255, 255, 0.7)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}


.custom-select.is-invalid,
.form-control.is-invalid,
.was-validated .custom-select:invalid,
.was-validated .form-control:invalid {
    border-color: #ff5e52;
}

/* MODALS */
.modal-header {
    position: relative;
    border-bottom: none;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
}

.modal-header .close {
    margin-left: 0;
    float: none;
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 40px;
    z-index: 1;
}

.modal-title {
    font-size: 35px;
}

.modal-body {
    font-size: 20px;
    padding-bottom: 30px;
}

.modal-footer {
    justify-content: center;
}

.popover_header_alone .arrow:after {
    border-top-color: #f7f7f7;
}

@media (min-width: 576px) {
    .modal-dialog {
        max-width: 770px;
    }
    .modal-body,
    .modal-footer {
        padding-left: 100px;
        padding-right: 100px;
    }
}