.CO2Chart {
  &__Container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .legend {
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 16px 0 0 0;
      list-style-type: none;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      font-size: 12px;
      color: #545454;

      &-item {
        margin-bottom: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &-text {
        padding: 4px 16px;
        display: inline-block;
        border-radius: 32px;
        background: white;
        boc-shadow: 0 17px 18px rgba(0, 0, 0, .2);
        border: 1px solid rgba(0, 0, 0, .2);
      }

      &-icon {
        display: inline-block;
        margin: 0 8px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: #cccccc;
      }
    }
  }

  &__ChartWrapper {
    width: 578px;
    height: 100%;
  }
}