@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";
@import 'mixins/v';
//@import 'mixins/calculateRem';

.App {

  .MuiButton-contained {
    box-shadow: none;
  }

  .MuiButton-containedPrimary {
    background: v(primaryMain) v(gradientPrimary);

    &:hover {
      background: v(primaryMain);
      box-shadow: 0 9px 7px v(shadowPrimary);
    }
  }

  .MuiButton-containedSecondary {
    background: v(secondaryMain) v(gradientSecondary);

    &:hover {
      background: v(secondaryMain);
      box-shadow: 0 9px 7px v(shadowSecondary);
    }
  }

  .MuiButtonBase-root {
    &:focus {
      outline: none;
    }
  }

  label.Mui-focused {
    color: v(textPrimary);
  }

  .Mui-focused fieldset {
    color: v(textPrimary);
  }

  .MuiInput-underline {
    &:before {
      border-bottom-color: v(inputUnderline);
    }

    &:after {
      border-bottom-color: v(textPrimary);
    }
  }

  .MuiInputAdornment-root .MuiSvgIcon-root {
    font-size: 16px;
  }

  .MuiFormControl-root.form-control {
    width: 100%;
  }

  .MuiTypography-body1 {
    color: v(textPrimary);
  }

  .MuiFormLabel-root {
    overflow: hidden;
    max-width: 90%;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .MuiFormLabel-root.Mui-error {
    color: v(errorMain);
  }

  .required-field {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 10px;
    height: 100%;
    position: absolute;
    left: 0;
    top: 50%;
    transform: scale(1.1) translate3d(-10px, -5px, 0);
    color: v(linkMain);

    &.required-field-date {
      transform: scale(1.1) translate3d(3px, -30px, 0);
      @include media-breakpoint-only(xs) {
        transform: scale(1.1) translate3d(3px, -40px, 0);
      }
      @include media-breakpoint-only(sm) {
        transform: scale(1.1) translate3d(3px, -40px, 0);
      }
    }

    &.required-field-checkbox {
      transform: scale(1.1) translate3d(-11px, -17px, 0);
    }

    &.error {
      color: v(errorMain);
    }
  }

  .form-group {
    @include media-breakpoint-only(xs) {
      margin-bottom: 0;
    }
    @include media-breakpoint-only(sm) {
      margin-bottom: 0;
    }
  }

  .form-input-label-mob {
    @include media-breakpoint-only(xs) {
      font-size: 14px;
    }
    @include media-breakpoint-only(sm) {
      font-size: 14px;
    }
  }

  .form-col {
    min-height: 60px;

    @include media-breakpoint-only(xs) {
      min-height: 75px;
    }
    @include media-breakpoint-only(sm) {
      min-height: 75px;
    }
  }

  .form-col-mob {
    @include media-breakpoint-only(xs) {
      min-height: 45px;
    }
    @include media-breakpoint-only(sm) {
      min-height: 45px;
    }
  }

  .form-subtitle {
    color: rgba(84, 84, 84, 0.2);
  }

  .form-checkbox-small {
    transform: translate3d(30px, -6px, 0);

    a {
      color: v(linkMain);

      &:hover {
        color: v(linkMain);
      }
    }
  }

  .google-places-autocomplete {
    z-index: 30;

    .MuiFormControl-root {
      margin: 0;
    }

    .MuiFormLabel-root.Mui-error {
      color: v(textSecondary);
    }

    .suggestion-container {
      padding: 5px;
      background-color: #fafafa;
      box-shadow: 0 9px 14px rgba(4, 13, 37, 0.04), 0 1px 8px rgba(4, 13, 37, 0.08);
      border-radius: 2px;
      font-size: 14px;
    }

    .suggestion {
      padding: 5px;

      &:hover {
        cursor: pointer;
        background-color: rgba(234, 243, 245, 0.7);
      }
    }
  }

  .KeyboardDatePicker {
    .MuiFormLabel-root.Mui-error {
      color: v(textSecondary);
    }
  }
}

.App .MuiButton-textPrimary, .modal-content .MuiButton-textPrimary {
  background: v(gradientPrimary);
}

.App .MuiButton-textSecondary, .modal-content .MuiButton-textSecondary {
  background: v(gradientSecondary);
}

.App .MuiCheckbox-colorSecondary.Mui-checked, .modal-content .MuiCheckbox-colorSecondary.Mui-checked {
  color: v(checkboxBackground);
}

.App .MuiButton-root, .modal-content .MuiButton-root {
  min-height: 45px;
  min-width: 180px;
  border-radius: 32px;
  text-transform: initial;
  transition: background-color 0.45s linear, box-shadow 0.45s linear;
  color: v(primaryContrastText);

  &:focus {
    outline: none;
  }

  &.error {
    background: v(errorMain) v(gradientError);
    color: v(white);

    &:hover {
      background: v(errorMain);
      box-shadow: 0 9px 7px v(shadowError);
    }
  }

  &.link {
    background: v(linkMain) v(gradientLink);
    color: v(white);

    &:hover {
      background: v(linkMain);
      box-shadow: 0 9px 7px v(shadowLink);
    }
  }
}

.MuiMenu-list {
  background-color: v(backgroundGray);
  border-radius: 2px;
  box-shadow: 0 9px 14px rgba(4, 13, 37, 0.04), 1px 8px rgba(4, 13, 37, 0.08);

  .MuiMenuItem-root {
    font-size: 14px;
  }
}

.MuiListItem-root.Mui-selected, .MuiListItem-root.Mui-selected:hover {
  background-color: v(linkMainDark) !important;
}

.MuiListItem-button:hover {
  background-color: v(linkMainDark) !important;
}

#menu-Country {
  .MuiPaper-root {
    max-height: 200px;

    &::-webkit-scrollbar {
      height: 8px;
      width: 6px;
    }

    &::-webkit-scrollbar-track-piece {
      background: transparent;
    }

    &::-webkit-scrollbar-thumb:vertical,
    &::-webkit-scrollbar-thumb:horizontal {
      background: v(scrollBarColor);
      border-radius: 20px;
    }
  }

  /*.MuiList-root {
    padding: 0;
  }*/
}

.App .MuiAvatar-root.MuiAvatar-colorDefault {
  background-color: v(avatarBackground);
}

.App .MuiAvatar-root {
  width: 36px;
  height: 36px;
}